export const renderPagination = (currentPage, totalPages, onPageChange) => {
  const paginationContainer = document.getElementById("pagination");

  const pagination = new Pagination(paginationContainer);
  if (!paginationContainer) return;

  paginationContainer.innerHTML = "";
  if (totalPages <= 1) return;

  const prevButton = document.createElement("button");
  prevButton.classList.add("prev-button");
  prevButton.textContent = "←";
  prevButton.disabled = currentPage === 1;
  prevButton.addEventListener("click", () => {
    onPageChange(currentPage - 1);
  });
  if (prevButton.disabled) {
    prevButton.style.backgroundColor = "#7a854e";
  } else {
    prevButton.style.backgroundColor = "#9BB537";
  }

  const nextButton = document.createElement("button");
  nextButton.classList.add("next-button");
  nextButton.textContent = "→";
  nextButton.disabled = currentPage === totalPages;

  nextButton.addEventListener("click", () => {
    onPageChange(currentPage + 1);
  });

  if (currentPage >= totalPages) {
    nextButton.disabled = true;
    nextButton.style.backgroundColor = "#7a854e";
    nextButton.style.cursor = "not-allowed";
  } else {
    nextButton.disabled = false;
    nextButton.style.backgroundColor = "#9BB537";
    nextButton.style.cursor = "pointer";
  }
  paginationContainer.appendChild(prevButton);

  for (let i = 1; i <= totalPages; i++) {
    const pageButton = document.createElement("button");
    pageButton.textContent = i;
    pageButton.classList.add("page-button");
    if (i === currentPage) {
      pageButton.classList.add("active");
    }

    pageButton.addEventListener("click", () => {
      onPageChange(i);
    });
    paginationContainer.appendChild(pageButton);
  }
  paginationContainer.appendChild(nextButton);
};
